





















import Vue from "vue";
import { ISubmitCallback } from "./types";

type FormRef = {
  validate(): boolean;
  resetValidation(): void;
};

export default Vue.extend({
  name: "CreatePostForm",
  data() {
    return {
      submitInProgress: false,
      model: {
        content: ""
      },
      rules: {
        content: [
          (value: string | undefined) => !!value || "Сообщение обязательно"
        ]
      }
    };
  },
  props: {
    onSubmit: {
      type: (Function as unknown) as () => ISubmitCallback,
      required: true
    }
  },
  methods: {
    submitHandler() {
      const formRef = (this.$refs.form as unknown) as FormRef;

      if (!formRef.validate()) {
        return;
      }

      this.submitInProgress = true;
      this.onSubmit(this.model)
        .then(() => {
          this.resetModel();
          formRef.resetValidation();
        })
        .catch(error => {
          throw error;
        })
        .finally(() => {
          this.submitInProgress = false;
        });
    },
    resetModel() {
      this.model.content = "";
    }
  }
});
