











import Vue from "vue";
import { IAuthor } from "../../domain";

export default Vue.extend({
  name: "AuthorAvatar",
  props: {
    author: {
      type: Object as () => IAuthor,
      required: true
    },
    small: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  computed: {
    avatarSize(): number {
      return this.small ? 46 : 84;
    }
  }
});
